// NOT FOUND :: START

.notFound {
  overflow: hidden;
  position: relative;
  @include fluid(min-height, 320px, 1920px, 431px, 640px);
  @include fluid(padding-top, 320px, 1920px, 50px, 147px);

  &__bg {
    overflow: hidden;
    z-index: 0;
    position: absolute;
    top: 50px;
    left: 0;
    width: 100%;
    @include fluid(height, 320px, 1920px, 431px, 581px);
    background-repeat: no-repeat;
    background-position: bottom center;
    @include respond(wide-medium) {
      background-image: url('../img/bg.svg');
      background-size: cover;
    }
    @include respond(max-wide-small) {
      background-image: url('../img/img-bg.png');
    }
    @include respond(max-small) {
      top: 20px;
      background-position-x: 40%;
    }
    @include respond(large) {
      top: 60px;
    }
  }

  .c-grid {
    z-index: 1;
    position: relative;
    @include respond(max-extra-small) {
      padding-left: 25px;
      padding-right: 25px;
    }
  }

  &__wrapper {
    @include fluid(padding-top, 320px, 1920px, 75px, 96px);
  }

  &__img {
    display: block;
    max-width: 100%;
    height: auto;
    margin-left: auto;
    margin-right: auto;
    @include respond(medium) {
      width: 430px;
    }

    &-wrap {
      padding-left: 35px;
      padding-right: 35px;
      @include fluid(padding-bottom, 320px, 1920px, 18px, 36px);
    }
  }

  &__message {
    text-align: center;
    //font-size: 60px;
    font-weight: bold;
    @include fluid(font-size, 320px, 1920px, 18px, 64px);
  }
}

// NOT FOUND :: END